<script setup lang="ts">
import { GoToRoomDetail } from '@/commons/Global';
import { CreateRoom } from '@/commons/twoonone/call';
import BaseDialog from '@/components/BaseDialog.vue';
import { ref } from 'vue';

const [dl_body, dl_display, dl_action] = [ref(''), ref(false), () => GoToRoomDetail(new_room_hash)]
let new_room_hash = ''
async function create_room_action() {
    const resp = await CreateRoom()
    if (typeof resp === 'number') {
        console.error(resp)
        return
    }
    new_room_hash = resp.room_hash
    dl_body.value = '创建房间成功，即将跳转到房间详细页'
    dl_display.value = true
}
await create_room_action()
</script>
 
<template>
    <BaseDialog :-body='dl_body' v-model="dl_display" :-close-action="dl_action"/>
</template>